(function(document ,$, undefined){
    "use strict";

    var linkToMathsTutorial = "//zestedesavoir.com/tutoriels/244/comment-rediger-des-maths-sur-zeste-de-savoir/";
    var linkToMarkdownHelp = "//zestedesavoir.com/tutoriels/221/rediger-sur-zds/";
    var linkToPygments = "http://pygments.org/languages";

    var tips = [
        "Les simples retours √† la ligne ne sont pas pris en compte. Pour cr√©er un nouveau paragraphe, pensez √† <em>sauter une ligne</em> !",
        "Encadrez votre texte par une ast√©risque pour √©crire en italique ou deux pour du gras.",
        "Pour √©crire un lien, utilisez la syntaxe <code>[texte de lien](url du lien)</code>",
        "Les blocs de code sont d√©limit√©s par trois accents graves <code>```</code>.",
        "Pour cr√©er une liste √† puces, sautez une ligne et commencez chaque √©l√©ment de la liste par <code>+</code>, <code>-</code> ou <code>*</code>.",
        "Pour citer quelqu'un, utilisez le symbole <code>></code>.",
        "Pour tracer une ligne, utilisez <code>---</code>, <code>+++</code> ou <code>***</code>.",
        "Pour aller √† la ligne sans changer de paragraphe, finissez votre premi√®re ligne par deux espaces.",
        "Pour √©crire un bout de code au milieu d‚Äôune phrase, utilisez la syntaxe <code>`un bout de code`</code>.",
        "Le langage d‚Äôun bloc de code peut √™tre sp√©cifi√© apr√®s les <code>```</code> ouvrants. La liste des langages support√©s <a href=\"" + linkToPygments + "\">est disponible ici</a>.",
        "Vous pouvez <a href=\"" + linkToMathsTutorial + "\">√©crire des formules math√©matiques</a> en encadrant ces derni√®res du signe dollar <code>$</code>."
    ];
    
    function addDocMD($elem){
        $elem.each(function(){
            var $help = $("<div/>", {
                "class": "markdown-help",
                "html": "<strong>Astuce :</strong> " + tips[Math.floor(Math.random() * tips.length)] + " <a href=\"" + linkToMarkdownHelp + "\">Envie d'en savoir plus ?</a>"
            });
            $(this).after($help);
        });
    }
    

    $(document).ready(function(){
        if ($("body").data("show-markdown-help")) {
            addDocMD($(".md-editor"));
            $("#content").on("DOMNodeInserted", ".md-editor", function(e){
                var $editor = $(e.target);
                if($editor.next().hasClass("markdown-help") === false) {
                    addDocMD($editor);
                }
            });
        }
    });
})(document, jQuery);
